import React,{useState} from 'react'
import { Link } from 'react-router-dom'
import './index.scss'
import SelectedSKU from 'Page/StockEntry/Repacking/WeightEntry/SelectedSKUList/SelectedSKU'
const SelectedSKUList = ({
    hideSKUList,
    setActiveSKU,
    activeSku,
    moduleEntry,
    moduleName
}) => {
    const selectedSku = moduleEntry.jobPost.list.length
    const dimention = document.documentElement.clientWidth
    let height = document.documentElement.clientHeight
    height = height-212
    const [hideContent, setHideContent] = useState(false)
    return (
        <div className={`${hideSKUList ? 'w-0' : 'w-30'} overflow-hidden  mt-md-0 rounded-bottom rounded-top`}>
            <div style={{padding:10}} className='w-100 bg-white border-top border-left border-right rounded-top border-soft-green text d-flex align-items-center justify-content-between' onClick={() => dimention <= 500 ? setHideContent(!hideContent) : null}>
                <div className="LatoBold"> Selected SKU <span className='font-green'>{`(${selectedSku} SKU${selectedSku > 1 ? 's' : ''})`}</span></div>
                <div className='d-flex align-items-center justify-content-between'>
                    <Link tabIndex='0' className='sku-item' to='/inventory-entry/repacking/weight-entry/remove-sku'>
                        <div className="fontSizeTitle px-3 emptyTextField iconU-remove" />
                    </Link>
                    <Link tabIndex='0' className='sku-item' to='/inventory-entry/repacking/weight-entry/add-sku'>
                        <div className='fontSizeTitle emptyTextField text-bold iconU-entryAdd' ></div>
                    </Link>

                </div>
            </div>
            <div style={{height: dimention <= 500 ? false : height, maxHeight:height}} className={` overflow-y w-100 bg-white mr-0 border rounded-bottom border-soft-green pl-3 pr-0 pt-3 ${hideContent ? 'd-none' : null}`}>
                {
                    moduleEntry?.jobPost.list.map((data, idx) =>
                        <SelectedSKU
                            data={data}
                            idx={idx}
                            activeSku={activeSku}
                            skuDescription={data?.skuName}
                            skuNumber={data?.skuNumber}
                            uomLabel={data?.unitOfMeassures}
                            uomValue={data?.totalQty}
                            uom={data?.unitOfMeassures}
                            img={data?.photo}
                            onClick={() => setActiveSKU(idx)}
                        />)
                }
            </div>
        </div>
    )
}

export default SelectedSKUList