import React from 'react'
const Pack = ({
    isEmpty,
    entryListIdx,
    wmsPoSkuInfoId,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    placeholder,
    grade,
    title,
    className,
    packThreshold
}) => {
    const show = uom === 'gram' || uom === 'kg' ? false : true
    return (
        <div className={` ${inputWidth} mb-3 mr-3 ${className} ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={`${isEmpty ? 'tooltip-custom border-red' : 'tooltip-custom1  border'} rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mr-3`}>
                <input maxLength='10' autoComplete='off' type='text' className='w-100 plain-input' value={values} name='pack' placeholder={placeholder} onChange={(e) => onChange(e)} />
                <div className='font-soft-grey'>{uom}</div>
                <span className={`${isEmpty ? 'tooltiptext' : 'd-none'}`}>
                    <span style={{ fontWeight: 600 }}>Threshold Error</span>
                    <br />
                    <span>{`Cannot be larger than 50%. Your current threshold is ${packThreshold}%`}</span>
                </span>
            </div>
        </div>
    )
}

export default Pack