import { isInteger } from "formik";

export const next = ({
  e,
  state,
  isCaptcha,
  setIsValidated,
  setIsType,
  isType,
}) => {
  const key = e.key; // const {key} = event; ES6+
  // console.log(key, state, isType[state]);
  let arr_tmp = ['ArrowRight', 'ArrowLeft', 'ArrowUp', 'ArrowDown', 'Tab']
  if (arr_tmp.includes(key)) {
    return 0;
  } else if (key === "Backspace") {
    if (state !== 1) {
      document.getElementById("t" + (parseInt(state) - 1)).focus();
    }
  } else {
    if (state !== 3) {
      document.getElementById("t" + (parseInt(state) + 1)).focus();
    }
  }

  let type = [...isType];
  if (key === "Backspace") {
    type[state] = '';
  } else {
    type[state] = key.toUpperCase();
  }
  let char = "QWERTYUIOPLKJHGFDSAZXCVBNM1234567890"
  let allowedChar = char.split('')
  if (allowedChar.includes(key.toUpperCase()) || type[state] == '') {
    document.getElementById("t" + (parseInt(state))).value = type[state];
    setIsType(type);
  } else {
    return 0;
  }

  let stat = true;
  isCaptcha.map((data, index) => {
    if (index == 0) {
      return;
    }
    if (!(type[index + 1] == isCaptcha[index])) {
      stat = false;
    }
  });
  // console.log(isCaptcha);
  // console.log(type);
  setIsValidated(stat);
};

export const skuListUpdateCheck = (module, noSkuChecking, moduleName) => {
  if(module?.length){
    let newModule = []
    module.map(list => {
      list.poList.map(po => {
        po.itemList.map(item => {
          const {qty, grade} = item
          const _qty = isInteger(qty) ? parseInt(qty) : (!qty ? 0 : parseFloat(qty))
          const _sku = noSkuChecking ? list.skuName : moduleName === 'waste' ? (item.wasteType?.label ? item.wasteType?.label : list.skuName) : grade?.label
          const _toWaste = moduleName === 'waste' ?( item.wasteType?.label ==='Semi Waste' || item.wasteType?.label ==='Waste' ? true :false) : grade?.label ==='Waste' || grade?.label === 'Semi Waste' ? true : false
          const _uom = noSkuChecking ? list.unitOfMeassures : grade?.uom  === 'gram' ? 'kg' : grade?.uom
          let newSku = {
            sku:_sku,
            qty:_qty,
            uom:_uom,
            wqty:0,
            swqty:0
          }
          if(_toWaste){
            newSku.qty = 0
            newSku.sku = list.skuName
          }
          if( moduleName === 'waste' ? item.wasteType?.label === 'Waste' : grade?.label === 'Waste') newSku.wqty = _qty
          if( moduleName === 'waste' ? item.wasteType?.label === 'Semi Waste' : grade?.label === 'Semi Waste') newSku.swqty = _qty
          const isExist = newModule.findIndex(sku => sku?.sku === (_toWaste ? list.skuName : _sku))
          
          if(moduleName !== 'waste'){
            if(isExist === -1) newModule.push(newSku)
            if(isExist !== -1){
              if(!_toWaste) newModule[isExist].qty += _qty
              if(grade?.label ==='Semi Waste')newModule[isExist].swqty += _qty
              if(grade?.label ==='Waste')newModule[isExist].wqty += _qty
            }
          }

          if(moduleName === 'waste'){
            if(isExist === -1) newModule.push(newSku)
            if(isExist !== -1){
              if(!_toWaste) newModule[isExist].qty += _qty
              if(item.wasteType?.label ==='Semi Waste')newModule[isExist].swqty += _qty
              if(item.wasteType?.label ==='Waste')newModule[isExist].wqty += _qty
            }
          }
          
        })
      })
    })
    const skuName = module.length ? module[0].skuName : 'No SKU'
    const i = newModule.findIndex(s => s.sku === skuName)
    newModule.map(sku => {
      if(i !== -1){
        if(sku.sku === 'Waste') newModule[i].wqty = sku.qty
        if(sku.sku === 'Semi Waste') newModule[i].swqty = sku.qty
      }
    })
    return newModule
  }
}