import React from 'react'
const Pack = ({
    isEmpty,
    entryListIdx,
    wmsPoSkuInfoId,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    placeholder,
    grade,
    title,
    className,
    packThreshold
}) => {
    const show = uom === 'gram' || uom === 'kg' ? false : true
    return (
        <div className={` mb-3 mr-3 ${className} ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={` w-100 d-flex align-items-center input-height px-2 mr-3`}>
                <div className={`LatoBold ${inputWidth} ${isEmpty ? 'red' : 'green'}`}>{`${packThreshold ? `${packThreshold}%` : '0%'}`}</div>
            </div>
        </div>
    )
}

export default Pack