import React from 'react'
import { useDispatch } from 'react-redux'
import WeightEntryCard from 'Page/StockEntry/Repacking/WeightEntry/WeightEntryList/WeightEntryCard'
import { removeEntryModule, saveJobSheet } from 'apiServices'
import { submitValidation } from 'Page/StockEntry/Repacking/services'
import { ModalsConfirmation as Confirmation } from 'Component/Modal/ModalConfirmation'
import PopUpSaved from "Component/Modal/PopUpSaved";
import PopUpRequired from "Component/Modal/PopUpRequired";
import Discard from 'Component/Modal/PopUpDiscard'
import { useEntryDimention, useRepackingEntry } from 'Page/StockEntry/GeneralServices'
import CustomTable from 'Component/CustomTable'
import Dropdown from 'Component/Dropdown'

const WeightEntryList = ({
    hideSKUList,
    setHideSKUList,
    activeSku,
    moduleEntry,
    moduleName
}) => {
    const dispatch = useDispatch()
    
    const {
        isLoading,
        invalidThreshold,
        setInvalidThreshold,
        setIsSuccess,
        confirmation,
        setConfirmation,
        discard,
        setDiscard,
        isSaved,
        setIsSaved,
        action,
        setAction,
        isRequired,
        setIsRequired,
        hideContent,
        setHideContent,
        unitOfMeassures,
        totalWeight,
        edited,
        postInfo,
        skuName,
        skuNumber,
        poList,
        gradeList,
        totalPack,
        showTargetRepack,
        table
      } = useRepackingEntry({moduleEntry,activeSku})

      const {dimention,height,heightAdj} = useEntryDimention(showTargetRepack)
    
    //Condition
    const isMobile = dimention <= 500
    const isHidden = hideContent ? 'd-none' : null
    const isPlural = totalWeight > 1 ? "s" : ''
    const isPluralPack = totalPack > 1 ? "s" : ''
    const isEdited = edited ? 'bg-green' : 'bg-lineGrey'
    const showTarget = showTargetRepack ? null : 'd-none'
    return (
        <div style={{paddingLeft:dimention <= 500 ? 0 : 10}} className='w-100 h-100 mt-3 mt-md-0'>
            <Discard
                discard={discard}
                module={'repacking'}
                moduleEntry={moduleEntry}
                close={() => setDiscard(false)}
                discardEntry={(id) => removeEntryModule({ id, moduleName, dispatch })}
            />
            <PopUpSaved
                moduleEntry={moduleEntry}
                submit={() => dispatch({ type: 'CREATE_JOB_SHEET', [`${moduleName}EntryData`]: null, [`${moduleName}SummaryData`]: null })}
                confirmation={confirmation}
                setConfirmation={setConfirmation}
                isSaved={isSaved}
                action={action}
                setIsSaved={setIsSaved}
                moduleName={moduleName}
                totalSku={moduleEntry?.jobPost?.list.length}

            />
            <PopUpRequired
                isRequired={isRequired}
                module={moduleEntry}
                invalidThreshold={invalidThreshold}
                setIsRequired={setIsRequired}
                setInvalidThreshold={setInvalidThreshold}
                moduleName={moduleName}
                totalSku={moduleEntry?.jobPost?.list.length}

            />
            <Confirmation
                moduleName={moduleName}
                isLoading={isLoading}
                moduleEntry={moduleEntry}
                confirmation={confirmation}
                setConfirmation={setConfirmation}
                isSuccess={postInfo}
                setAction={setAction}
                action={action}
                save={() => saveJobSheet({ name: 'save', moduleEntry, moduleName, dispatch, setIsSuccess,table })}
                submit={() => saveJobSheet({ name: 'submit', moduleEntry, moduleName, dispatch, setIsSuccess,table })}
            />
            <div className='rounded-bottom rounded-top'>
                <div className={`target-repack mb-2 p-3 bg-white rounded border-top border-left border-right border-bottom rounded-top border-soft-green ${showTarget}`} style={{maxHeight:280,minHeight:280, overflow:'hidden'}}>
                    <div className={`d-flex justify-content-between mb-3 align-items-center`}>
                        <div className='LatoBold'>Target Repack Summary</div>
                        <Dropdown options={[{label:'Slot 0',value:0},{label:'Slot 1',value:1},{label:'Slot 2',value:2}]} selectedValue={{label:`Slot ${table?.data[0]?.slot?.toString()}`, value:table?.data[0]?.slot}} show={true} width={'w-25'} placeholder='Slot' title={'Slot'} style={{marginRight:0}} noMarginBottom={true}/>
                    </div>
                    <CustomTable 
                        editColumn={'false'}
                        styles={{color:'red', textAlign:'right', width:'100%', display: showTargetRepack ? false : 'none'}} 
                        data={table?.data} 
                        columns={table?.columns}/>
                </div>
                <div style={{height}} style={{padding:isMobile ? 0 : 10}} className={`bg-white w-100 mr-0 d-flex align-items-center border-top border-left border-right border-bottom rounded-top border-soft-green`}>
                    <div className='d-flex w-100 m-0 p-0' onClick={() => isMobile ? setHideContent(!hideContent) : null}>
                        <div className={`${hideSKUList ? 'iconU-sideBarHideRight' : 'iconU-sideBarHideLeft'} emptyTextField fontSizeTitle mr-3 m-0 p-0`} onClick={() => setHideSKUList(!hideSKUList)} />
                        <div className='mr-3 pr-1 d-flex align-items-center m-0 p-0 text LatoBold'>Weight Entry</div>
                        <div className='mr-3 iconU-sideBarDot d-flex align-items-center m-0 p-0 text' />
                        <div className='mr-3 d-flex align-items-center font-green LatoBold m-0 p-0'>{`${skuName} (${skuNumber})`}</div>
                    </div>
                    {/* <Sort moduleEntry = {moduleEntry} moduleName = {moduleName} activeSku = {activeSku} dispatch = {dispatch}/> */}
                </div>
                <div style={{height:isMobile ? height : heightAdj, minHeight:isMobile ? height : heightAdj}} className={`w-100 bg-white overflow-y mr-0 border-left border-right border-soft-green pl-3 pr-2 pt-3 ${isHidden}`}>
                    {
                        poList?.map((poItem, idx) => {
                            return <WeightEntryCard
                                gradeList={gradeList}
                                inputWidth='input-w-100'
                                activeSku={activeSku}
                                poListIdx={idx}
                                poItem={poItem}
                                moduleEntry={moduleEntry}
                                moduleName={moduleName}
                            />
                        })
                    }

                </div>
                <div className='w-100 d-flex align-items-center p-3 border-left border-right border-bottom rounded-bottom LatoBold bg-white'>
                    {/* <button
                        type="button"
                        className={`btn ${isEdited}  text-white px-5 mr-3 LatoBold`}
                        onClick={() => edited ? setConfirmation({ show: true, name: 'save' }) : null}>
                        Save
                    </button> */}

                    <button
                        tabIndex='0'
                        type="button"
                        className={`btn bg-lineGrey sku-item text-white px-5 mr-3 LatoBold`}>
                        Save
                    </button>
                    <div className='d-flex align-items-center'>
                        <div>Total Pack <span className='font-green mr-3'>{`${totalPack} ${'PACK'}${isPluralPack}`}</span></div>
                        <div className='rounded-circle bg-dark mr-3' style={{width:5, height:5}}></div>
                        <div>Total Weight <span className='font-green'>{`${totalWeight} ${unitOfMeassures?.toUpperCase()}${isPlural}`}</span></div>                        
                    </div>
                </div>
            </div>
            <div className="d-flex align-items-center justify-content-end mt-3">
                <div tabIndex='0' onClick={() => setDiscard(true)} className='red pr-3 sku-item fontSizeButton cursor-pointer'><i className='iconU-entryTrash'></i> Discard</div>
                <button tabIndex='0' onClick={() => edited ? submitValidation({ moduleEntry, moduleName, dispatch, setConfirmation, setIsRequired, setInvalidThreshold }) : null} className={`btn px-5 sku-item py-2 text-white ${isEdited} LatoBold no-shadow`} style={{ height: "fit-content !important" }} >Submit</button>
            </div>
        </div>
    )
}

export default WeightEntryList